<template>
  <div>
    <bread-nav :nav="nav"></bread-nav>
    <div class="main">
      <div class="content">
        <p class="title">{{activityDetail.title}}</p>
        <p class="prompt">
          <span>发布时间：{{activityDetail.createdTime | substrTime}}</span>
          <span style="float: right;">截止时间：{{activityDetail.endDate | substrTime}}</span>
        </p>
        <p v-html="activityDetail.content"></p>
      </div>
      <p class="participate">共 {{table.total}} 人参与</p>

      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="20%">姓名</th>
          <th width="20%">参与时间</th>
          <th width="20%">子女</th>
          <th width="20%">手机号</th>
          <th width="20%">操作</th>
        </tr></thead>
        <tbody>
        <tr v-for="item in table.list">
          <td>{{item.parentUserName}}</td>
          <td>{{item.createdTime | substrTime }}</td>
          <td>{{item.studentName}}</td>
          <td>{{item.phone}}</td>
          <td>
            <span class="oper" @click="onViewClick(item)">查看附件</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>

      <!--查看附件-->
      <el-dialog title="附件列表"
                 :visible.sync="dialogVisible"
                 width="30%">
        <div v-for="(annexItem,annexIdx) in annexList" class="annex">
          <a :href="annexItem.fileUrl" target="_blank">附件{{annexIdx+1}}</a>
        </div>
        <div style="text-align: center;margin-top: 20px">
          <div class="btn-submit" @click="dialogVisible = false">返回</div>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      let vm = this
      return {
        id: vm.$route.query.id,
        nav: [{name: '活动管理', path: 'go(-1)'}, {name: '查看活动', path: ''}],
        activityDetail: [],
        table: {
          url: '/notice/queryNoticeParticipationRecordPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {}
        },
        // 查看附件
        dialogVisible: false,
        annexList: []
      }
    },
    methods: {
      onViewClick (item) {
        this.dialogVisible = true
        this.annexList = item.attachmentList
      }
    },
    async mounted () {
      this.activityDetail = await this.$ajax({
        url: '/notice/getNotice.do',
        el: '.content',
        query: {
          id: this.id
        }
      })
      this.table.query.id = this.id
      this.$search(this.table)
    }
  }
</script>

