    position: absolute
    top: $top-nav-height+60px
    left: $left-nav-width+20px
    right: 20px
    border-radius: 5px
    background-color: #fff
    padding: 30px 15px 0 15px
    .content
      margin-bottom: 60px
      .title
        font-size: $font-large+2px
